import React, { useEffect, useState, useRef } from 'react'
import { useRA } from '../../contexts/RAContext'
import type { ScreenType, TransitionType, TransitionDirection } from '../../types/screens'
import '../../styles/ar-screen.css'

interface ARScreenProps {
  onNavigate: (screen: ScreenType, transition?: TransitionType, direction?: TransitionDirection) => void
  title?: string
  subtitle?: string
  backgroundImage?: string
}

export const ARScreen: React.FC<ARScreenProps> = ({
  onNavigate
}) => {
  const { raData } = useRA()
  const config = raData?.configuracoes || {}
  const usarAFrame = config.usarAFrame !== false
  const debugVisual = config.debugVisual !== false
  const [arLoading, setArLoading] = useState(true)
  const [trackingStarted, setTrackingStarted] = useState(false)
  const [targetFound, setTargetFound] = useState(false)
  const [debugInfo, setDebugInfo] = useState<any>(null)
  const hideIntervalRef = useRef<NodeJS.Timeout | null>(null)
  const loadingTimeoutRef = useRef<NodeJS.Timeout | null>(null)
  const arLoadingRef = useRef(true)
  const targetFoundRef = useRef(false) // Ref para acessar o valor atualizado do target

  // Get base URL
  const getBaseUrl = () => {
    const base = (import.meta as any)?.env?.BASE_URL || (document?.baseURI ? new URL(document.baseURI).pathname : '/')
    const b = base && base !== '/' ? (base.endsWith('/') ? base : base + '/') : '/'
    return b === '/' ? '' : b.endsWith('/') ? b.slice(0, -1) : b
  }

  const baseUrl = getBaseUrl()
  const normalizePath = (path: string) => {
    if (baseUrl === '') {
      return path.startsWith('/') ? path : `/${path}`
    }
    const cleanPath = path.startsWith('/') ? path.slice(1) : path
    return `${baseUrl}/${cleanPath}`
  }

  // Configura√ß√£o do MindAR - seguindo o exemplo exato
  const imageTargetSrc = normalizePath('assets/mindar/targets.mind')
  const targetImageSrc = normalizePath('assets/images/target-image.png')

  // Configurar MindAR seguindo EXATAMENTE o exemplo HTML
  useEffect(() => {
    if (!usarAFrame) {
      setArLoading(false)
      return
    }

    const setupMindAR = async () => {
      try {
        // Detectar se √© mobile (usado em v√°rios lugares)
        const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)

        // Aguardar A-Frame e MindAR estarem prontos
        await new Promise<void>((resolve) => {
          let attempts = 0
          const maxAttempts = 100
          const checkReady = () => {
            attempts++
            const aframeReady = (window as any).AFRAME
            const mindarReady = aframeReady && aframeReady.components && aframeReady.components['mindar-image']

            if (aframeReady && mindarReady) {
              console.log('‚úÖ A-Frame e MindAR prontos')
              resolve()
            } else if (attempts >= maxAttempts) {
              console.warn('‚ö†Ô∏è MindAR n√£o detectado ap√≥s timeout, continuando...')
              resolve()
            } else {
              setTimeout(checkReady, 100)
            }
          }
          checkReady()
        })

        // Verificar se scene existe
        let sceneEl = document.querySelector('a-scene#ar-scene-main') as any

        // Se n√£o existe ou n√£o tem mindar-image, criar/recriar corretamente
        if (!sceneEl || !sceneEl.hasAttribute('mindar-image')) {
          console.log('üîÑ Criando a-scene com mindar-image desde o in√≠cio (como no exemplo HTML)...')

          // Remover scene antigo se existir
          if (sceneEl) {
            try {
              if (sceneEl.parentNode) {
                sceneEl.parentNode.removeChild(sceneEl)
              } else {
                sceneEl.remove()
              }
            } catch (e) {
              console.warn('Erro ao remover scene antigo:', e)
            }
            await new Promise(resolve => requestAnimationFrame(resolve))
          }

          // Criar novo scene EXATAMENTE como no exemplo HTML
          const newScene = document.createElement('a-scene')
          newScene.id = 'ar-scene-main'
          newScene.setAttribute('embedded', 'true')
          // Configura√ß√£o do MindAR com op√ß√µes otimizadas para mobile
          const mindarConfig = isMobile
            ? `imageTargetSrc: ${imageTargetSrc}; maxTrack: 1; warmupTolerance: 0.1; missTolerance: 0.3;`
            : `imageTargetSrc: ${imageTargetSrc};`
          newScene.setAttribute('mindar-image', mindarConfig)
          newScene.setAttribute('color-space', 'sRGB')
          newScene.setAttribute('renderer', 'colorManagement: true, physicallyCorrectLights')
          newScene.setAttribute('vr-mode-ui', 'enabled: false')
          newScene.setAttribute('ar-mode-ui', 'enabled: false')
          newScene.setAttribute('webxr', 'enabled: false')
          newScene.setAttribute('device-orientation-permission-ui', 'enabled: true')

          // Estilos
          newScene.style.position = 'fixed'
          newScene.style.top = '0'
          newScene.style.left = '0'
          newScene.style.width = '100%'
          newScene.style.height = '100%'
          newScene.style.zIndex = '1'
          newScene.style.background = 'transparent'

          // Criar a-camera ANTES de adicionar ao DOM (como no exemplo)
          const camera = document.createElement('a-camera')
          camera.setAttribute('position', '0 0 0')
          camera.setAttribute('look-controls', 'enabled: false')
          newScene.appendChild(camera)

          // Criar a-assets (como no exemplo)
          const assets = document.createElement('a-assets')
          const targetImg = document.createElement('img')
          targetImg.id = 'card'
          targetImg.src = targetImageSrc
          targetImg.crossOrigin = 'anonymous'
          assets.appendChild(targetImg)
          newScene.appendChild(assets)

          // Criar a-entity com mindar-image-target (como no exemplo)
          const targetEntity = document.createElement('a-entity')
          targetEntity.setAttribute('mindar-image-target', 'targetIndex: 0')

          // Adicionar a-plane (como no exemplo)
          const arPlane = document.createElement('a-plane')
          arPlane.setAttribute('src', '#card')
          arPlane.setAttribute('position', '0 0 0')
          arPlane.setAttribute('height', '0.552')
          arPlane.setAttribute('width', '1')
          arPlane.setAttribute('rotation', '0 0 0')
          targetEntity.appendChild(arPlane)

          // Adicionar cubo 3D
          const arCube = document.createElement('a-box')
          arCube.setAttribute('position', '0 0.1 0')
          arCube.setAttribute('rotation', '0 45 0')
          arCube.setAttribute('color', '#4CC3D9')
          arCube.setAttribute('scale', '0.2 0.2 0.2')
          arCube.setAttribute('animation', 'property: rotation; to: 0 405 0; dur: 2000; easing: linear; loop: true')
          targetEntity.appendChild(arCube)

          newScene.appendChild(targetEntity)

          // Adicionar ao DOM
          document.body.appendChild(newScene)

          // Aguardar scene inicializar
          await new Promise<void>((resolve) => {
            let attempts = 0
            const maxAttempts = 50
            const checkReady = () => {
              attempts++
              const scene = document.querySelector('a-scene#ar-scene-main') as any
              if (scene && scene.hasLoaded) {
                resolve()
              } else if (attempts >= maxAttempts) {
                console.warn('‚ö†Ô∏è Timeout aguardando scene carregar')
                resolve()
              } else {
                setTimeout(checkReady, 100)
              }
            }
            checkReady()
          })

          sceneEl = document.querySelector('a-scene#ar-scene-main') as any
          console.log('‚úÖ Scene criado com mindar-image desde o in√≠cio')

          // No mobile, adicionar um pequeno delay para garantir sincroniza√ß√£o
          if (isMobile) {
            await new Promise(resolve => setTimeout(resolve, 500))
          }
        } else {
          console.log('‚úÖ Scene j√° tem mindar-image configurado')
        }

        // Esconder elementos VR (mas N√ÉO o modal de orienta√ß√£o - ele √© necess√°rio!)
        const hideVRElements = () => {
          // Esconder bot√µes VR/AR
          const vrButtons = document.querySelectorAll(
            '.a-enter-vr-button, .a-enter-ar-button, [class*="vr-button"], [class*="enter-vr"], [class*="enter-ar"], [class*="cardboard"]'
          )
          vrButtons.forEach((btn: any) => {
            if (btn) {
              btn.style.display = 'none'
              btn.style.visibility = 'hidden'
              btn.style.opacity = '0'
              btn.style.pointerEvents = 'none'
            }
          })

          // Esconder TODOS os modais de VR/cardboard (incluindo "Insert phone into cardboard holder")
          // Mas N√ÉO esconder o modal de permiss√£o de orienta√ß√£o (device-orientation-permission-ui)
          const allModals = document.querySelectorAll('div[class*="modal"], div[class*="ui"]')
          allModals.forEach((modal: any) => {
            if (modal) {
              const text = modal.textContent || modal.innerText || ''
              const hasCardboard = text.toLowerCase().includes('cardboard') ||
                text.toLowerCase().includes('insert phone') ||
                modal.classList.contains('a-vr-modal') ||
                (modal.className && modal.className.includes('cardboard'))

              // Se for modal de VR/cardboard, esconder
              if (hasCardboard) {
                modal.style.display = 'none'
                modal.style.visibility = 'hidden'
                modal.style.opacity = '0'
                modal.style.pointerEvents = 'none'
              }
              // Se for modal de permiss√£o de orienta√ß√£o (device-orientation-permission-ui), N√ÉO esconder
              else if (modal.classList.contains('a-orientation-modal') ||
                modal.classList.contains('a-orientation-permission-ui') ||
                (modal.className && modal.className.includes('orientation-permission'))) {
                // Deixar vis√≠vel - √© necess√°rio para ativar sensores
              }
            }
          })
        }

        hideVRElements()
        hideIntervalRef.current = setInterval(hideVRElements, 500)

        // Garantir que o v√≠deo esteja pronto antes de continuar
        const video = document.getElementById('arjs-video') as HTMLVideoElement
        if (video) {
          // Aguardar v√≠deo estar pronto (especialmente importante no mobile)
          await new Promise<void>((resolve) => {
            if (video.readyState >= 2) {
              // V√≠deo j√° est√° pronto
              resolve()
            } else {
              const onCanPlay = () => {
                video.removeEventListener('canplay', onCanPlay)
                video.removeEventListener('loadedmetadata', onCanPlay)
                resolve()
              }
              video.addEventListener('canplay', onCanPlay, { once: true })
              video.addEventListener('loadedmetadata', onCanPlay, { once: true })

              // Timeout de seguran√ßa
              setTimeout(() => {
                video.removeEventListener('canplay', onCanPlay)
                video.removeEventListener('loadedmetadata', onCanPlay)
                console.warn('‚ö†Ô∏è Timeout aguardando v√≠deo estar pronto')
                resolve()
              }, 5000)
            }
          })

          // Garantir que o v√≠deo esteja tocando (cr√≠tico para mobile)
          if (video.paused) {
            try {
              video.muted = true
              await video.play()
              console.log('‚úÖ V√≠deo iniciado para MindAR')
            } catch (err) {
              console.warn('‚ö†Ô∏è Erro ao iniciar v√≠deo:', err)
            }
          }

          // Garantir que o transform est√° correto baseado na configura√ß√£o
          const shouldMirror = config.cameraMirror !== undefined
            ? config.cameraMirror
            : (config.cameraFacing === 'user')

          if (shouldMirror) {
            video.style.transform = 'scaleX(-1)'
          } else {
            video.style.transform = 'scaleX(1)'
          }

          video.style.display = 'block'
          video.style.visibility = 'visible'
          video.style.opacity = '1'
          video.classList.add('ar-video-visible')

          // Garantir atributos mobile
          video.setAttribute('playsinline', '')
          video.setAttribute('webkit-playsinline', '')
          video.setAttribute('autoplay', '')
          video.muted = true

          console.log('üìπ V√≠deo tornado vis√≠vel para MindAR', {
            mirror: shouldMirror,
            config: config.cameraMirror,
            readyState: video.readyState,
            paused: video.paused,
            videoWidth: video.videoWidth,
            videoHeight: video.videoHeight,
            isMobile: isMobile
          })

          // No mobile, aguardar um frame adicional para garantir que tudo est√° sincronizado
          if (isMobile) {
            await new Promise(resolve => requestAnimationFrame(resolve))
          }
        } else {
          console.warn('‚ö†Ô∏è V√≠deo n√£o encontrado! O MindAR pode n√£o funcionar corretamente.')
        }

        // Event listeners do MindAR
        const onLoaded = (event: any) => {
          console.log('‚úÖ MindAR image target carregado', event)
          arLoadingRef.current = false
          setArLoading(false)
          setTrackingStarted(true)

          if (loadingTimeoutRef.current) {
            clearTimeout(loadingTimeoutRef.current)
            loadingTimeoutRef.current = null
          }

          if (debugVisual) {
            setDebugInfo((prev: any) => ({
              ...(prev || {}),
              mindarLoaded: true,
              targetIndex: 0,
              imageTargetSrc,
              loadedAt: new Date().toLocaleTimeString(),
              timestamp: new Date().toLocaleTimeString()
            }))
          }
        }

        const onFound = (event: any) => {
          console.log('‚úÖ Target encontrado!', event)
          targetFoundRef.current = true
          setTargetFound(true)

          if (debugVisual) {
            setDebugInfo((prev: any) => {
              const newInfo = {
                ...(prev || {}),
                targetFound: true,
                targetFoundTime: new Date().toLocaleTimeString(),
                timestamp: new Date().toLocaleTimeString()
              }
              console.log('üîç Debug atualizado (target found):', newInfo)
              return newInfo
            })
          }
        }

        const onLost = (event: any) => {
          console.log('‚ö†Ô∏è Target perdido', event)
          targetFoundRef.current = false
          setTargetFound(false)

          if (debugVisual) {
            setDebugInfo((prev: any) => {
              const newInfo = {
                ...(prev || {}),
                targetFound: false,
                timestamp: new Date().toLocaleTimeString()
              }
              console.log('üîç Debug atualizado (target lost):', newInfo)
              return newInfo
            })
          }
        }

        const onError = (event: any) => {
          console.error('‚ùå Erro no MindAR:', event)
          if (debugVisual) {
            setDebugInfo((prev: any) => ({
              ...prev,
              error: event.detail || 'Erro desconhecido'
            }))
          }
        }

        // Adicionar listeners no scene
        sceneEl.addEventListener('mindar-image-loaded', onLoaded)
        sceneEl.addEventListener('mindar-image-target-found', onFound)
        sceneEl.addEventListener('mindar-image-target-lost', onLost)
        sceneEl.addEventListener('mindar-image-error', onError)

        // TAMB√âM adicionar listeners na entidade target (os eventos podem ser disparados l√°)
        const targetEntity = sceneEl.querySelector('[mindar-image-target]')
        if (targetEntity) {
          console.log('üìå Adicionando listeners na entidade target tamb√©m')
          targetEntity.addEventListener('targetFound', onFound)
          targetEntity.addEventListener('targetLost', onLost)
        }

        // Os eventos targetFound/targetLost j√° est√£o funcionando corretamente
        // N√£o precisamos de polling que pode causar conflito

        // Verificar se j√° est√° carregado (dar mais tempo no mobile)
        const checkDelay = isMobile ? 3000 : 2000
        setTimeout(() => {
          const mindarSystem = sceneEl.systems && sceneEl.systems['mindar-image-system']
          if (mindarSystem && mindarSystem.anchorEntities && mindarSystem.anchorEntities.length > 0) {
            console.log('‚úÖ MindAR j√° configurado', {
              anchorEntities: mindarSystem.anchorEntities.length,
              isMobile: isMobile
            })
            onLoaded({ detail: 'already-loaded' })
          } else {
            console.warn('‚ö†Ô∏è MindAR system n√£o encontrado ou n√£o inicializado', {
              hasSystem: !!mindarSystem,
              isMobile: isMobile
            })
          }
        }, checkDelay)

        // Timeout de seguran√ßa
        loadingTimeoutRef.current = setTimeout(() => {
          if (arLoadingRef.current) {
            console.warn('‚ö†Ô∏è MindAR n√£o inicializou no tempo esperado')
            arLoadingRef.current = false
            setArLoading(false)
          }
        }, 10000)

        // Debug interval
        let debugInterval: NodeJS.Timeout | null = null
        if (debugVisual) {
          debugInterval = setInterval(() => {
            const video = document.getElementById('arjs-video') as HTMLVideoElement
            const scene = document.querySelector('a-scene#ar-scene-main') as any

            setDebugInfo((prev: any) => {
              // Preservar targetFound e targetFoundTime quando atualizar
              return {
                ...(prev || {}),
                videoReady: video && video.readyState >= 2,
                videoWidth: video?.videoWidth,
                videoHeight: video?.videoHeight,
                sceneReady: scene && scene.hasLoaded,
                timestamp: new Date().toLocaleTimeString(),
                // Preservar targetFound se j√° foi definido
                targetFound: prev?.targetFound !== undefined ? prev.targetFound : false,
                targetFoundTime: prev?.targetFoundTime || null
              }
            })
          }, 1000)
        }

        // Cleanup
        return () => {
          sceneEl.removeEventListener('mindar-image-loaded', onLoaded)
          sceneEl.removeEventListener('mindar-image-target-found', onFound)
          sceneEl.removeEventListener('mindar-image-target-lost', onLost)
          sceneEl.removeEventListener('mindar-image-error', onError)

          // Remover listeners da entidade target
          const targetEntity = sceneEl.querySelector('[mindar-image-target]')
          if (targetEntity) {
            targetEntity.removeEventListener('targetFound', onFound)
            targetEntity.removeEventListener('targetLost', onLost)
          }

          if (debugInterval) {
            clearInterval(debugInterval)
          }
          if (loadingTimeoutRef.current) {
            clearTimeout(loadingTimeoutRef.current)
            loadingTimeoutRef.current = null
          }
        }

      } catch (err) {
        console.error('‚ùå Erro ao configurar MindAR:', err)
        arLoadingRef.current = false
        setArLoading(false)
        if (loadingTimeoutRef.current) {
          clearTimeout(loadingTimeoutRef.current)
          loadingTimeoutRef.current = null
        }
      }
    }

    setupMindAR()

    // Cleanup
    return () => {
      if (hideIntervalRef.current) {
        clearInterval(hideIntervalRef.current)
        hideIntervalRef.current = null
      }
    }
  }, [usarAFrame, imageTargetSrc, targetImageSrc, debugVisual])

  return (
    <div className="ar-game-screen">
      {/* Loading overlay */}
      {arLoading && (
        <div className="ar-loading-overlay">
          <div className="ar-loading-content">
            <div className="ar-loading-spinner"></div>
            <p className="ar-loading-text">Preparando AR...</p>
          </div>
        </div>
      )}

      {/* A-Frame Scene com MindAR */}
      {usarAFrame && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          zIndex: 1
        }}>
          {/* A-Frame Scene j√° est√° no DOM */}
        </div>
      )}

      {/* Instru√ß√µes */}
      {trackingStarted && !arLoading && (
        <div style={{
          position: 'fixed',
          bottom: '20px',
          left: '50%',
          transform: 'translateX(-50%)',
          background: 'rgba(0, 0, 0, 0.7)',
          color: 'white',
          padding: '15px 25px',
          borderRadius: '10px',
          zIndex: 10000,
          textAlign: 'center',
          fontSize: '16px'
        }}>
          {targetFound ? '‚úÖ Target detectado!' : 'Aponte a c√¢mera para a imagem alvo'}
        </div>
      )}

      {/* Debug Visual Panel */}
      {debugVisual && debugInfo && (
        <div style={{
          position: 'fixed',
          top: '10px',
          right: '10px',
          background: 'rgba(0, 0, 0, 0.8)',
          color: '#0f0',
          padding: '15px',
          borderRadius: '8px',
          zIndex: 10001,
          fontSize: '12px',
          fontFamily: 'monospace',
          maxWidth: '300px',
          border: '2px solid #0f0'
        }}>
          <div style={{ marginBottom: '10px', fontWeight: 'bold', borderBottom: '1px solid #0f0', paddingBottom: '5px' }}>
            üîç DEBUG INFO
          </div>
          <div style={{ lineHeight: '1.6' }}>
            <div>üì∑ C√¢mera: {config.cameraFacing || 'user'}</div>
            <div>ü™û Mirror: {config.cameraMirror !== false ? 'SIM' : 'N√ÉO'}</div>
            {debugInfo.videoWidth && (
              <>
                <div>üìê Resolu√ß√£o: {debugInfo.videoWidth}x{debugInfo.videoHeight}</div>
                <div>‚úÖ V√≠deo: {debugInfo.videoReady ? 'PRONTO' : 'CARREGANDO'}</div>
              </>
            )}
            <div>üéØ MindAR: {debugInfo.mindarLoaded ? '‚úÖ CARREGADO' : '‚è≥ CARREGANDO'}</div>
            <div>üìÅ Arquivo: {imageTargetSrc.split('/').pop()}</div>
            <div>üéØ Target: {debugInfo.targetFound ? '‚úÖ DETECTADO' : '‚ùå N√ÉO DETECTADO'}</div>
            {debugInfo.targetFoundTime && (
              <div>‚è∞ Detectado em: {debugInfo.targetFoundTime}</div>
            )}
            {debugInfo.error && (
              <div style={{ color: '#f00' }}>‚ùå Erro: {debugInfo.error}</div>
            )}
            <div>üïê {debugInfo.timestamp}</div>
          </div>
        </div>
      )}
    </div>
  )
}
