// Inicializa√ß√£o global - roda ANTES do React
// Cria o v√≠deo UMA VEZ e compartilha entre todos

let globalVideo: HTMLVideoElement | null = null
let globalStream: MediaStream | null = null
let initPromise: Promise<void> | null = null

interface RAConfiguracoes {
    usarAFrame?: boolean
    usarVideo?: boolean
    usarFaceTracking?: boolean
    cameraFacing?: 'user' | 'environment'
    cameraMirror?: boolean
    debugVisual?: boolean
}

// Carregar configura√ß√µes do RA
async function loadRAConfig(): Promise<RAConfiguracoes> {
    try {
        const base = (import.meta as any)?.env?.BASE_URL || (document?.baseURI ? new URL(document.baseURI).pathname : '/')
        const b = base && base !== '/' ? (base.endsWith('/') ? base : base + '/') : '/'
        const url = `${b}assets/data/ra.json`
        const response = await fetch(url)

        if (!response.ok) {
            console.warn('N√£o foi poss√≠vel carregar ra.json, usando configura√ß√µes padr√£o')
            return { usarAFrame: true, usarVideo: true, usarFaceTracking: true }
        }

        const data = await response.json()
        return data.configuracoes || { usarAFrame: true, usarVideo: true, usarFaceTracking: true }
    } catch (err) {
        console.warn('Erro ao carregar configura√ß√µes do RA, usando padr√£o:', err)
        return { usarAFrame: true, usarVideo: true, usarFaceTracking: true }
    }
}

export async function initializeGlobal(): Promise<void> {
    if (initPromise) return initPromise

    initPromise = (async () => {
        try {
            console.log('Iniciando inicializa√ß√£o global...')

            // Carregar configura√ß√µes do RA
            const config = await loadRAConfig()
            console.log('Configura√ß√µes do RA:', config)

            // 1. Aguardar e inicializar A-Frame se necess√°rio
            if (config.usarAFrame !== false) {
                console.log('Aguardando A-Frame...')
                await waitForAFrame()
                console.log('A-Frame pronto')

                // Inicializar A-Frame Scene
                console.log('Inicializando A-Frame Scene...')
                await initializeAFrameScene()
                console.log('A-Frame Scene inicializado')

                // Configurar listener para quando o modal aparecer
                setupModalListener()
            } else {
                console.log('A-Frame desabilitado nas configura√ß√µes')
            }

            // 2. Inicializar c√¢mera se necess√°rio
            if (config.usarVideo !== false) {
                console.log(`Inicializando c√¢mera (${config.cameraFacing || 'user'})...`)
                await initializeGlobalCamera(config)
                console.log('C√¢mera inicializada')
            } else {
                console.log('V√≠deo desabilitado nas configura√ß√µes')
            }

            // 3. Carregar face-api.js se necess√°rio
            if (config.usarFaceTracking !== false) {
                console.log('Carregando face-api.js...')
                await loadFaceApi()
                console.log('face-api.js carregado')
            } else {
                console.log('Face Tracking desabilitado nas configura√ß√µes')
            }

            console.log('Inicializa√ß√£o global conclu√≠da')
        } catch (error) {
            console.error('Erro na inicializa√ß√£o global:', error)
            throw error
        }
    })()

    return initPromise
}

// Fun√ß√£o para obter configura√ß√µes
export async function getRAConfig(): Promise<RAConfiguracoes> {
    return loadRAConfig()
}

// Fun√ß√£o para garantir que o modal seja clic√°vel
let modalConfigured = false
let modalObserver: MutationObserver | null = null
let modalCheckTimeout: NodeJS.Timeout | null = null

function setupModalListener() {
    // Se j√° foi configurado, n√£o fazer nada
    if (modalConfigured && modalObserver) {
        return
    }

    // Fun√ß√£o para configurar o modal uma vez
    const configureModal = (modal: Element) => {
        if (modalConfigured) return

        // Marcar como configurado
        modalConfigured = true

        // Garantir que o modal seja clic√°vel
        const modalEl = modal as HTMLElement
        modalEl.style.pointerEvents = 'auto'
        modalEl.style.zIndex = '2147483647'
        modalEl.style.position = 'fixed'
        modalEl.style.display = 'block'
        modalEl.style.visibility = 'visible'
        modalEl.style.opacity = '1'

        // Garantir que todos os filhos sejam clic√°veis
        const allChildren = modal.querySelectorAll('*')
        allChildren.forEach((child) => {
            (child as HTMLElement).style.pointerEvents = 'auto'
        })

        // Garantir que bot√µes sejam clic√°veis
        const buttons = modal.querySelectorAll('button')
        buttons.forEach((button) => {
            button.style.pointerEvents = 'auto'
            button.style.cursor = 'pointer'
            button.style.zIndex = '2147483647'
            button.style.position = 'relative'
        })

        // Desconectar observer ap√≥s configurar
        if (modalObserver) {
            modalObserver.disconnect()
            modalObserver = null
        }
    }

    // Debounce para evitar execu√ß√µes excessivas
    let debounceTimer: NodeJS.Timeout | null = null

    // Observar quando o modal aparecer
    modalObserver = new MutationObserver(() => {
        // Debounce: aguardar 200ms antes de verificar
        if (debounceTimer) {
            clearTimeout(debounceTimer)
        }

        debounceTimer = setTimeout(() => {
            if (modalConfigured) return

            const modal = document.querySelector('.a-orientation-modal') ||
                document.querySelector('[class*="orientation-modal"]') ||
                document.querySelector('[class*="a-orientation"]') ||
                document.querySelector('[class*="permission"]')

            if (modal) {
                configureModal(modal)
            }
        }, 200)
    })

    modalObserver.observe(document.body, {
        childList: true,
        subtree: true,
        attributes: true,
        attributeFilter: ['class', 'style']
    })

    // Verificar imediatamente uma vez
    if (modalCheckTimeout) {
        clearTimeout(modalCheckTimeout)
    }

    modalCheckTimeout = setTimeout(() => {
        if (modalConfigured) return

        const modal = document.querySelector('.a-orientation-modal') ||
            document.querySelector('[class*="orientation-modal"]') ||
            document.querySelector('[class*="a-orientation"]')

        if (modal) {
            configureModal(modal)
        }
    }, 1000)
}

function waitForAFrame(): Promise<void> {
    return new Promise((resolve) => {
        if (typeof window !== 'undefined' && (window as any).AFRAME) {
            resolve()
            return
        }

        const checkAFrame = () => {
            if (typeof window !== 'undefined' && (window as any).AFRAME) {
                resolve()
            } else {
                setTimeout(checkAFrame, 100)
            }
        }
        checkAFrame()
    })
}


async function initializeGlobalCamera(config?: RAConfiguracoes): Promise<MediaStream | null> {
    try {
        // Se j√° existe v√≠deo e stream, reutilizar
        const existingVideo = document.getElementById('arjs-video') as HTMLVideoElement
        if (existingVideo && existingVideo.srcObject) {
            globalVideo = existingVideo
            globalStream = existingVideo.srcObject as MediaStream
            return globalStream
        }

        // Usar configura√ß√£o do JSON ou padr√£o 'user' (c√¢mera frontal)
        const facingMode = config?.cameraFacing || 'user'
        // Respeitar explicitamente o cameraMirror do JSON
        // Se n√£o especificado, usar padr√£o baseado no facingMode:
        // - user (frontal): espelhar por padr√£o (comportamento natural)
        // - environment (traseira): n√£o espelhar por padr√£o
        const shouldMirror = config?.cameraMirror !== undefined
            ? config.cameraMirror
            : (facingMode === 'user') // Padr√£o: espelhar apenas se for frontal

        console.log(`üì∑ Configurando c√¢mera: facing=${facingMode}, mirror=${shouldMirror} (config: ${config?.cameraMirror})`)

        // Detectar se √© mobile
        const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)

        // Criar novo stream com configura√ß√µes otimizadas para mobile
        const stream = await navigator.mediaDevices.getUserMedia({
            video: {
                // Mobile: usar resolu√ß√µes mais baixas e flex√≠veis
                width: isMobile ? { ideal: 640, max: 1280 } : { ideal: 1280 },
                height: isMobile ? { ideal: 480, max: 720 } : { ideal: 720 },
                facingMode: { ideal: facingMode },
                // Mobile: adicionar constraints adicionais
                ...(isMobile && {
                    frameRate: { ideal: 30, max: 30 }
                })
            },
            audio: false
        })

        // Criar v√≠deo se n√£o existir
        if (!existingVideo) {
            const video = document.createElement('video')
            video.id = 'arjs-video'
            video.setAttribute('playsinline', '')
            video.setAttribute('webkit-playsinline', '') // iOS Safari
            video.setAttribute('autoplay', '')
            video.setAttribute('muted', 'true')
            video.muted = true
            video.style.position = 'fixed'
            video.style.top = '0'
            video.style.left = '0'
            video.style.width = '100vw'
            video.style.height = '100vh'
            video.style.objectFit = 'cover'
            video.style.zIndex = '0'
            video.style.display = 'none'
            video.style.visibility = 'hidden'
            video.style.opacity = '0'

            // Aplicar espelhamento baseado na configura√ß√£o
            if (shouldMirror) {
                video.style.transform = 'scaleX(-1)'
                console.log('ü™û C√¢mera ESPELHADA (mirror=true)')
            } else {
                video.style.transform = 'scaleX(1)'
                console.log('üì∑ C√¢mera N√ÉO ESPELHADA (mirror=false)')
            }

            document.body.appendChild(video)
            globalVideo = video
        } else {
            globalVideo = existingVideo
            // Aplicar espelhamento no v√≠deo existente
            if (shouldMirror) {
                globalVideo.style.transform = 'scaleX(-1)'
                console.log('ü™û V√≠deo existente ESPELHADO')
            } else {
                globalVideo.style.transform = 'scaleX(1)'
                console.log('üì∑ V√≠deo existente N√ÉO ESPELHADO')
            }
        }

        globalVideo.srcObject = stream
        globalStream = stream
        await globalVideo.play()

        console.log('‚úÖ C√¢mera inicializada:', {
            facing: facingMode,
            mirror: shouldMirror,
            mirrorConfig: config?.cameraMirror,
            width: stream.getVideoTracks()[0]?.getSettings().width,
            height: stream.getVideoTracks()[0]?.getSettings().height
        })

        return stream
    } catch (err) {
        console.warn('Erro ao inicializar c√¢mera global:', err)
        return null
    }
}

async function loadFaceApi(): Promise<void> {
    if ((window as any).faceapi) {
        return
    }

    return new Promise((resolve, reject) => {
        const script = document.createElement('script')
        script.src = 'https://cdn.jsdelivr.net/npm/face-api.js@0.22.2/dist/face-api.min.js'
        script.onload = () => {
            console.log('face-api.js carregado')
            resolve()
        }
        script.onerror = () => reject(new Error('Failed to load face-api.js'))
        document.head.appendChild(script)
    })
}

async function initializeAFrameScene(): Promise<void> {
    // Para MindAR funcionar corretamente, o scene DEVE ser criado com mindar-image desde o in√≠cio
    // O ARScreen vai criar o scene completo com todos os atributos necess√°rios
    // Aqui apenas verificamos se j√° existe
    if (document.querySelector('a-scene')) {
        console.log('A-Frame Scene j√° existe')
        return
    }

    // N√£o criar scene aqui - deixar o ARScreen criar com mindar-image desde o in√≠cio
    // Isso evita problemas de timing e garante que o MindAR inicialize corretamente
    console.log('A-Frame Scene ser√° criado pelo ARScreen com mindar-image desde o in√≠cio')
}

export function getGlobalVideo(): HTMLVideoElement | null {
    return globalVideo || (document.getElementById('arjs-video') as HTMLVideoElement)
}

export function getGlobalStream(): MediaStream | null {
    return globalStream || (getGlobalVideo()?.srcObject as MediaStream) || null
}

